body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 2rem;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.navigation{
    font-size: 1.2rem;
    margin-bottom: 5px; 
}
.navigation ul{
    text-align:left;
}
.navigation  ul li a{
    color:black;
    text-decoration: none;
    padding: 5px;
}
.navigation  ul li{
    border:none;
    font-size: .9rem;
    text-align:center;
    background: white;
    max-width: 20%;
    float: left;
    margin-right: 5%;
    
}
ul {
  padding:0;
  list-style:none;
  width: 90%;
  margin:auto;
}

li{
  font-size:1.2rem;
  line-height: 2rem;
  border-bottom: 1px dotted #777;
}
.text-area{
    height: 100px;
    width:500px;
}
.post-tweet{
    padding:2px;
    font-size: 1rem;
}
.like-btn{
    font-size: 1rem;
    background: #1da1f2;
    color:white;
    margin-left: 5px;
}
.liked{
    background: #1da1f2;
}
